@use "./Shared.scss" as shared;

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: shared.$primary-color;
  height: 100%;
}

h1,
h2 {
  color: shared.$secondary-dark-color;
  margin: 0.4em 0;
}

h3,
h4,
h5,
h6,
p,
ul,
details {
  margin: 0.4em 0;
  color: shared.$secondary-color;
}

@media (max-width: 768px) {
  h1 {
    font-size: shared.$FSH1;
  }

  h2 {
    font-size: shared.$FSH2;
  }

  h3 {
    font-size: shared.$FSH3;
  }

  h4,
  p,
  span,
  ul,
  label,
  input,
  button {
    font-size: shared.$FSH4;
  }

  h5 {
    font-size: shared.$FSH5;
  }

  h6 {
    font-size: shared.$FSH6;
  }
}

a {
  color: shared.$accent1-color;
  // text-decoration: none;
}

* {
  font-family: shared.$primary-font-fam;
}


button,
input {
  cursor: pointer;
  padding-inline: 0.5em;
  margin: 0.2em;
  border-radius: 1em;
  background-color: shared.$accent1-color;
  color: shared.$primary-color;
  border: 0.1em solid shared.$accent1-color;
  // text-align: center;

  &:hover,
  &:focus-visible {
    background-color: shared.$primary-color;
    color: shared.$accent1-color;
    border: 0.1em solid shared.$accent1-color;
    outline: none;
  }
}

:root {
  .tooltip {
    display: 'flex';
    flex-direction: 'column';
    color: shared.$primary-color;
    background-color: shared.$secondary-color;
    border-radius: 0%;
    border: 1px solid shared.$accent1-color;
    max-width: 50vw;
    text-wrap: wrap;
    text-align: justify;
  }

  .tooltip .tooltipArrow {
    background-color: shared.$accent1-color;
  }
}