@use "./../../styles/Shared" as shared;


$default_margin: 0.6em;
$PP_size: 20vw;
$QR_size: 16vw;
$max_size: 200px;

$canvas_bg : white;
$default_border_size : 0.1em;

.MyCV {

  .introductive_informations {
    border-bottom: $default_border_size solid shared.$secondary-dark-color;
    display: grid;
    grid-template-columns: 25% 75%;
    align-items: center;
    // background-color: shared.$secondary-color;

    .personality_picture {
      place-self: center;
      display: flex;
      justify-content: center;

      margin: 1em;
      width: $PP_size;
      height: $PP_size;
      max-width: $max_size;
      max-height: $max_size;
      overflow: hidden;


      border-radius: 50%;
      border: $default_margin solid shared.$secondary-dark-color;
      // outline: 0.2em solid shared.$secondary-dark-color;


      img {
        max-width: 100%;
        margin: auto;
      }
    }

    .immatricule {
      display: flex;
      align-items: center;
      flex-wrap: wrap;

      .immatricule_written {
        margin: $default_margin;
        margin-right: auto;
      }

      .qr_codes {
        display: flex;
        flex-wrap: wrap;

        div.qr_code {
          margin: $default_margin;
          border: $default_margin solid $canvas_bg;
          outline: 1px solid shared.$accent1-color;
          position: relative;
          width: $QR_size;
          height: $QR_size;
          max-width: $max_size;
          max-height: $max_size;

          &:hover {
            box-shadow: calc($default_margin/3) calc($default_margin/-3) 0px shared.$accent1-color;
          }

          & canvas {
            height: 100% !important;
            width: 100% !important;
          }

          & img {
            background-color: $canvas_bg;
            border: 0.2em solid $canvas_bg;
            border-radius: 50%;
            // margin: $default_margin;

            $img_size: 30%;
            width: $img_size; //picture size + border size
            position: absolute;
            left: calc(50% - $img_size/2);
            top: calc(50% - $img_size/2);
          }
        }
      }
    }
  }

  .experience_content {
    display: grid;
    justify-content: space-around;
    grid-template-columns: 25% 75%;

    .personality_profile {
      align-items: center;
      background-color: shared.$primary-color;
      border-right: $default_border_size solid shared.$secondary-dark-color;
      // color: black;


      .profile_interests {
        margin: 0 $default_margin;

        .profile_interest {
          margin: $default_margin 0;
          display: flex;
          flex-direction: column;
          justify-content: space-around;

          .interest_header {
            display: flex;
            justify-content: space-between;
            // flex-wrap: wrap;
          }
        }

      }

      @media (max-width: 768px) {
        h2 {
          font-size: calc(shared.$FSH2 * 0.8);
        }

        h4,
        p {
          font-size: calc(shared.$FSH4 * 0.8);
        }
      }
    }

    .work_profile {

      .work_experiences {
        margin: 0 $default_margin;

        .work_experience {
          margin: $default_margin 0;

          header {
            display: grid;
            grid-template-columns: 50% 50%;

            h3:last-child {
              text-align: end;
            }
          }

          ul {
            list-style: none;
            padding-left: 0;
            margin: 0;
            text-align: justify;
          }

          .work_project {
            margin-block: $default_margin;

            li {
              margin-block: calc($default_margin/2);
            }
          }
        }

      }
    }
  }
}


.separator {
  width: 50%;
  border: $default_border_size solid;
  border-radius: $default_border_size;
  border-color: shared.$secondary-dark-color;
}


progress {
  width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  height: $default_margin;


  &::-webkit-progress-bar {
    background-color: shared.$primary-color;
    border-radius: $default_margin;
    border: 1px solid shared.$accent1-color;
  }

  &::-webkit-progress-value {
    background-color: shared.$accent1-color;
    print-color-adjust: exact;
    border-radius: inherit;
    // border: 100px shared.$accent1-color
  }
}

.progress_out {
  background-color: shared.$primary-color;
  height: $default_margin;
  border-radius: $default_margin;
  border: 1px solid shared.$accent1-color;

  &>.progress_in {
    background-color: shared.$accent1-color;
    height: 100%;
    border-radius: inherit;
  }
}