@use "./../../styles/Shared" as shared;


main.TSP {
    padding: 8px;

    section {
        display: flex;
        flex-direction: column;

        @media (max-width: 768px) {
            align-items: center;
        }

        max-width: 100vw;
    }

    details {
        padding-inline: 0.4em;

        text-wrap: nowrap;
    }

    aside.stored {
        display: flex;
        overflow: scroll;
        width: 100%;

        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }
    }


    div.solvers {
        justify-content: center;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;
        width: 100%;
    }

    div.Playground {
        display: flex;
        flex-flow: column;
        align-items: center;
        max-width: 100%;
    }

    .sidebar {
        width: 600px;
        max-width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-areas:
            " nohead nohead"
            " nohead nohead"
            " header header";

        button {
            grid-area: header;
        }

        &>* {
            place-self: center stretch;
        }
    }



    div.TSPState {
        max-width: 100%;

        &.user {
            width: 600px;
        }

        &.solver {
            width: 300px;
        }


        &>svg {
            border: 1px solid shared.$accent1-color;
        }

        ul {
            overflow: scroll;

            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }

        details {
            overflow: scroll;

            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }


    }

    .TSPStateSVG {

        path {
            stroke: shared.$secondary-color;
            stroke-linejoin: round;

            &.user {
                stroke: shared.$accent1-color;
            }
        }

        circle {
            cursor: pointer;

            &.active {

                fill: shared.$accent1-color
            }

            &.inactive {
                fill: shared.$secondary-dark-color
            }
        }

        text {
            user-select: none;
            pointer-events: none;
        }
    }
}